import { BarChart as MUIBarChart } from "@mui/x-charts/BarChart";
import { use } from "react";

interface IBarChart {
fetchSalesData: Promise<SalesDataType[]>;
}
const BarChart: React.FC<IBarChart> = ({ fetchSalesData }) => {
const salesData = use(fetchSalesData);
return (
<MUIBarChart
	xAxis={[{ scaleType: "band", dataKey: "name" }]}
	dataset={salesData}
	series={[{ dataKey: "sales", label: "Sales" }]}
	width={500}
	height={300}
/>
);
};
export default BarChart;