import React from 'react';
import logo from './logo.svg';
import './App.css';
import TopBarWrapper from './components/TopBarWrapper.tsx';
import DashboardPages from './pages/DashboardPage.tsx';
import { HashRouter as Router, Routes, Route } from 'react-router-dom';
import { AuthProvider } from './context/AuthContext.tsx';
import Login from './components/auth/Login.tsx';
import ProtectedRoute from './components/auth/ProtectedRoute.tsx';

function App(){
  return (
    <TopBarWrapper>
	<AuthProvider>
            <Router basename="/reactmatvideoplayer">
                <Routes>
                    <Route path="/login" element={<Login />} />
                    <Route
                        path="/"
                        element={
                            <ProtectedRoute>
                                <DashboardPages />
                            </ProtectedRoute>
                        }
                    />
                </Routes>
            </Router>
        </AuthProvider>
    </TopBarWrapper>
  );
}

export default App;
