import MenuIcon from "@mui/icons-material/Menu";
import {
AppBar,
Box,
IconButton,
Stack,
Toolbar,
Typography,
} from "@mui/material";
import * as React from "react";

interface ITopBarWrapper {
children: React.ReactNode;
}

const TopBarWrapper: React.FC<ITopBarWrapper> = ({ children }) => {
return (
<Stack minHeight="100dvh" width="100vw">
<Box width="100%">
<Box sx={{ flexGrow: 1 }}>
<AppBar position="static">
<Toolbar>
<IconButton
  size="large"
  edge="start"
  color="inherit"
  aria-label="menu"
  sx={{ mr:2 }}
  >
  <MenuIcon />
  </IconButton>
  <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
    React Dashboard
  </Typography>
</Toolbar>
</AppBar>
</Box>
</Box>
<Box width="100%">{children}</Box>
</Stack>
);
};

export default TopBarWrapper;